	import moment from 'moment'
	import util from '@/libs/util'
	import common from '@/libs/common.mixin'
	import monthPicker from 'c%/MonthPicker.vue'
	export default{
		mixins: [common],
		components: {
			monthPicker
		},
		data () {
			return {				
				data_uri: '',
				data: [],
				map_type: {1:'结转',2:'退费',3:'充值', 4:'下单', 5:'订单续费 ',0:'用户手动操作'},
				currentTime: '',
			}
		},
		mounted () {
			this.data_uri = 'students/'+ this.sid$ +'/money_history'
		},
		methods: {
			hasTitle (index) {
				if(index > 0){
					return moment(this.data[index].create_time).format('YYYYMM') != moment(this.data[index-1].create_time).format('YYYYMM')
				}
				return index==0
			},	
			getMonth (monthIndex){
				let result = String(this.data[monthIndex].create_time)
				
				return moment(result).format('MM月')
			},
			formatDate (row) {
				return moment(row).format('MM/DD')
			},
			showMonth (time) {
				time = moment(time).format('YYYYMMDD')
				this.$refs['month_picker'].show(time)
			},
			onChange (time) {	
				this.currentTime = time
				this.data_uri = util.sprintf('students/%s/money_history?year_month=%s',this.sid$,time)
				this.pageIndex = 0
				
				this.mescroll.resetUpScroll()
			},	
			deal_data (response) {
	        	let list = response.body.data.list,	
                	total = response.body.data.total,
                	curLength = 0
                	
                
                if(this.data_uri.indexOf('year_month')>-1&&list.length==0){
                	let currentTime = this.currentTime
                	let title = util.sprintf('%s年%s月',
								currentTime.substring(0,4),
								currentTime.substring(4,6))
                	this.$vux.alert.show({
					  title: '提示',
					  content: title+'没有考勤记录~'					  
					})
                	
                	this.mescroll.endBySize(this.data.length, this.data.length)
                	return 
                }	
                
            	if(this.pageIndex===1){
	        		this.data = []	        		
	        	}
	        	
	    		for(let l in list){	    			
	    			this.data = this.data.concat(list[l])	
	    			curLength += list[l].length
		        }
	    		this.get_students()
	    		this.mescroll.endBySize(curLength, total)
    		}
		}
	}



